		// console.log(store.state.mine.addedGroups)
		console.log(option)
		// let option = JSON.parse(option)
		let that = this;
		
		if (option.biz != undefined) {
			that.biz = option.biz;
		}
		if (that.biz == 'xz') {
			// 新增报销单据
			that.seltDeftGroup();
		} else if (that.biz == 'ck') {
			that.title = '查看报销单';
			that.edit = false;
			if (option.id != undefined) {
				that.rtid = option.id;
				console.log(11331,that.rtid)
				// 查询报销单据
				that.reqGetReimburse(that.rtid);
			}
		}
		uni.setNavigationBarTitle({
			title: that.title
		});
		
